<template>
  <div>
    <br>
    <div style="margin-right:1000px ;">
      <el-button @click="gotoWrite">Í∏Ä ÏûëÏÑ±</el-button>
    </div>
    <br/>
    <br/>
    <el-table
      :data="bbsList"
      style="width: auto;"
      border
    >
<!--      <el-table-column property="bid" label="Î≤àÌò∏">-->
<!--      </el-table-column>-->
      <el-table-column property="bname" label="Ïù¥Î¶Ñ">
      </el-table-column>
      <el-table-column property="bcontent" label="Ï†úÎ™©">
      </el-table-column>
      <el-table-column property="bcontent" label="ÎÇ¥Ïö©">
      </el-table-column>
      <el-table-column property="bdate" label="ÏûëÏÑ±ÏãúÍ∞Ñ">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from 'axios'
import { mapGetters } from "vuex";
export default {
  name: "Board",

  data() {
    return {
      projectVersion: 'Í≤åÏãúÌåê Î™©Î°ùv.12',
      bbsList: {}
    }
  },
  computed:{
    ...mapGetters({
      isLogin: 'isLogin'
    })
  },
  async created() {
    console.log('success!')
    await this.init()
  },
  methods: {
    async init() {
      await axios.post(
        '/api/native-query/board',
        {
                page: 0,
                size: 10
            }
      ).then(res => {
        console.log('res', res)
        this.bbsList = res.data.data.content
        }
      ).catch(err => {
        console.log(err)
      })
    },
    gotoWrite() {
      if(this.isLogin === false) {
        alert('Î°úÍ∑∏Ïù∏ ÌõÑ Ïù¥Ïö©Ìï¥Ï£ºÏÑ∏Ïöî.')
        this.$router.push('/login')
      } else {
        this.$router.push('/board/write')
      }
    }
  }
}
</script>

<style scoped>

</style>



