import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";
import createPersistedState from 'vuex-persistedstate';
Vue.use(Vuex)

// Î™®ÎìàÏùÄ ÎÇòÏ§ëÏóê Î∂ÑÎ¶¨
//import userStore from "./module/userStore";

const store = new Vuex.Store({
  //modules: userStore,
  //stateÎ•º localstorageÏóê Ï†ÄÏû•
  plugins: [
    createPersistedState()
  ],
  state: {//data
    userInfo: null,
    accessToken: '',
    isLogin: false,
    isLoginError: false
  },
  mutations: { //state Í∞íÏùÑ Î≥ÄÌôîÏãúÏºú Ï§ÄÎã§.
    //Î°úÍ∑∏Ïù∏Ïù¥ ÏÑ±Í≥µ ÌñàÏùÑ Îïå
    loginSuccess(state, payload) {
      let accessToken = payload.accessToken
      let userInfo = {
        username: payload.username,
        nickname: payload.nickname,
        authorityName: payload.authorityName
      }
      state.isLogin = true
      state.isLoginError = false
      state.accessToken = accessToken
      state.userInfo = userInfo
    },
    loginError(state) {
      state.isLogin = false
      state.isLoginError = true
    },
    logout(state) {
      console.log('logout')
      localStorage.removeItem('token')
      state.isLogin = false
      state.isLoginError = false
      state.userInfo = false
    }
  },
  actions: {
    //Ïú†Ï†Ä Ï†ïÎ≥¥ ÏñªÏñ¥Ïò§Í∏∞
    getMemberInfo({ commit }) {
      let token = localStorage.getItem("token")
      let config = {
        token: token
      }
      axios
        .post('/api/authenticate/getUserInfo', config)
        .then(response => {
          let userInfo = {
            username: response.data.username,
            nickname: response.data.nickname,
            authorityName: response.data.authorityName,
            accessToken: token
          }
          //ÌÜ†ÌÅ∞ Ï†ïÎ≥¥Í∞Ä Ïú†Ìö®ÌïòÎ©¥ stateÍ∞íÏùÑ Î≥ÄÌôîÏãúÏºúÏ£ºÎäî mutationsÏùò loginSuccessÏóê Í∞íÏùÑ ÎÑ£Ïñ¥Ï§ÄÎã§.
          commit('loginSuccess', userInfo)
        })
        .catch(error => {
          console.log(error)
          //ÌÜ†ÌÅ∞ Ï†ïÎ≥¥Í∞Ä Ïú†Ìö®ÌïòÏßÄ ÏïäÏúºÎ©¥ logoutÏùÑ ÌïúÎã§.
          commit('logout')
        })
    }
  },
  getters: { //computed Ïó≠Ìï†
    getToken: state => {
      return state.accessToken
    },
    isLogin: state => {
      return state.isLogin
    },
    getUserInfo: state => {
      return state.userInfo
    }
  }
});
export default store;



