import Vue, {computed} from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Board from '@/components/Board'
import BoardWrite from '@/components/BoardWrite'
import Login from '@/components/Login'
import store from "../store";
import {mapGetters} from "vuex";

Vue.use(Router)

//Í≤ΩÎ°ú Ïù∏Ï¶ù Ïú†Î¨¥ Ìï®Ïàò
const requireAuth = (to, from, next) => {
  //Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú mapGetterÎ°ú Í∞ÄÏ†∏Ïò§Í∏∞
  let isLogin = store.getters.isLogin
  if (isLogin === false) {
    console.log('Login Please!');
    console.log('isLogin', isLogin)
    //Î°úÍ∑∏Ïù∏Ïù¥ ÎêòÏñ¥ ÏûàÏßÄ ÏïäÏúºÎ©¥ /login ÌéòÏù¥ÏßÄÎ°ú Í∞ïÏ†ú Ïù¥Îèô
    return next('/login');
  } else {
    console.log('isLogin', isLogin)
    console.log("routing success : '" + to.path + "'");
    // Î°úÍ∑∏Ïù∏Ïù¥ ÎêòÏñ¥ ÏûàÏúºÎ©¥ ÌôîÎ©¥ Ï†ÑÌôò ÏÑ±Í≥µ...!
    next();
  }
}
//Î°úÍ∑∏Ïù∏Ïù¥ ÎêòÏñ¥ ÏûàÏúºÎ©¥ Ï†ëÍ∑º Î∂àÍ∞Ä Ìï®Ïàò
const loginPage = (to, from, next) => {
  //Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú mapGetterÎ°ú Í∞ÄÏ†∏Ïò§Í∏∞
  console.log('ÏïàÎÖïÌïòÏÑ∏Ïöî...')
  let isLogin = store.getters.isLogin
  if (isLogin === true) {
    console.log('you are aleady login!');
    console.log('isLogin', isLogin)
    return next('/');
  } else {
    console.log('isLogin', isLogin)
    console.log("routing success : '" + to.path + "'");
    next();
  }
}

//Í≤ΩÎ°ú Ï†úÏñ¥ Ï§ëÏïôÏÜå
const router = new Router(

  {

  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/board',
      name: 'Board',
      component: Board
    },
    {
      path: '/board/write',
      name: 'BoardWrite',
      component: BoardWrite,
      beforeEnter: requireAuth
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      beforeEnter: loginPage
    }
  ]
})

export default router



